<template>
  <div class="demo-wrapper">
    <div class="demo-input-event">
      <div>
        <p>enter</p>
        <r-input style="width: 300px" @enter="onEnter" />
        <p>change</p>
        <r-input style="width: 300px" @change="onChange" />
        <p>聚焦</p>
        <r-input style="width: 300px" @focus="onFocus" />
        <p>失焦</p>
        <r-input style="width: 300px" @blur="onBlur" />
        <p>清空</p>
        <r-input style="width: 300px" @clear="onClear" />
        <p>输入</p>
        <r-input style="width: 300px" @input="onInput"/>
      </div>
      <div class="demo-input-event-name">
        <p>触发事件：{{eve || '无'}}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      eve: "",
      value: ''
    };
  },
  methods: {
    onInput(v) {
      this.eve = 'input'
    },
    onEnter(v) {
      this.eve = 'enter'
    },
    onChange(v) {
      this.eve = 'change'
    },
    onFocus(v) {
      this.eve = 'focus'
    },
    onBlur(v) {
      this.eve = 'blur'
    },
    onClear(v) {
      this.eve = 'clear'
    },
  },
};
</script>
<style lang="scss">
  .demo-input-event{
    display: flex;
  }
  .demo-input-event-name{
    margin-left: 50px;
    p{
      font-size: 25px;
    }
  }
</style>
